.header {
    padding: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: darkslategray;
    color: azure;
}

.header_link {
    margin: 0 20px;
    color: azure;
    opacity: 1;
}

.header_link:hover {
    opacity: 0.7;
}